.App {
    font-family: "Poppins", sans-serif;
    background-color: #f8f8f8;
    min-height: 100vh;
}

.Main {
    padding-top: 150px;
}

.Content {
    background-color: #E5E1BF;
    padding: 10px;
    border-radius: 2px;
    border: 1px solid #dadadf;
    color: #0E3B43;
}

.FillerImage {
    object-fit: cover;
    height: 100%;
    width: 100%;
  }

.Image {
    max-height: 40vh;
    max-width: 100%;
    object-fit: contain;
}
  
* {
    box-sizing: border-box;
}
  
a {
    color: #357266;
    transition: color ease-in-out 0.3s;
    font-weight: bold;
}
  
a:hover {
    color: #0E3B43;
    text-decoration: none;
}
  
a:hover i {
    color: #0E3B43;
    transition: color ease-in-out 0.3s;
}

input[type="file"] {
    display: none;
}
  
i {
    color: #357266;
    font-size: 1.6rem;
    padding: 7px;
    vertical-align: middle;
}
  
i:hover {
    cursor: pointer;
    transition: color ease-in-out 0.3s;
    color: #0E3B43;
}

figure {
    text-align: center;
    display: inline-block;
}

p {
    color: #357266;
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    text-align: center;
 }

  
/* scrollbar */
*::-webkit-scrollbar {
    width: 5px;
}
*::-webkit-scrollbar-track {
    background: #f1f1f1;
}
*::-webkit-scrollbar-thumb {
    background: #888;
}
*::-webkit-scrollbar-thumb:hover {
    background: #555;
}